'use client';

import React, { useState } from 'react';
import { Sidebar } from './Sidebar';
import { Header } from './Header';
import { MainContent } from './MainContent';
import NotificationTray from './NotificationTray';

interface ShellLayoutProps {
  children?: React.ReactNode;
  initialSelectedDepartment?: string | null;
}

export function ShellLayout({ children, initialSelectedDepartment = null }: ShellLayoutProps) {
  const [selectedDepartment, setSelectedDepartment] = useState<string | null>(initialSelectedDepartment);

  return (
    <div className="flex h-screen bg-gray-50">
      {/* Sidebar */}
      <Sidebar 
        selectedDepartment={selectedDepartment}
        onDepartmentSelect={setSelectedDepartment}
      />
      
      {/* Main Content Area */}
      <div className="flex-1 flex flex-col overflow-hidden">
        <Header />
        <main className="relative flex-1 overflow-hidden">
          <div className="h-full flex">
            {/* Primary content area */}
            <div className="flex-1 overflow-auto">
              {selectedDepartment ? (
                <div className="h-full">
                  {/* This is where remote microfrontends will be loaded */}
                  <div className="p-6">
                    <h2 className="text-2xl font-semibold mb-4">
                      {selectedDepartment.charAt(0).toUpperCase() + selectedDepartment.slice(1)} Department
                    </h2>
                    <p className="text-gray-600">
                      Loading {selectedDepartment} application...
                    </p>
                  </div>
                </div>
              ) : (
                <MainContent />
              )}
              {children}
            </div>

            {/* Right panel after header */}
            <aside className="hidden lg:block w-72 shrink-0 border-l border-gray-200 bg-white/60 backdrop-blur supports-[backdrop-filter]:bg-white/50">
              <div className="pt-8 px-3 space-y-3">
                <div className="text-[13px] font-bold text-gray-800 tracking-tight">Priority Notifications and Incidents</div>
                <div className="border-b border-gray-200/80 -mx-3" />
                <NotificationTray embedded controls showClose />
              </div>
            </aside>
          </div>
        </main>
      </div>
    </div>
  );
}
